.movieItem {
  position: relative;
  width: calc(16.66% - 20px);
  height: 350px;
  margin: 10px;
  margin-top: 0px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

