.movieItem:hover {
  transform: scale(1.05); /* Slightly zoom in on hover */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.4); /* Enhance shadow on hover */
}

.movieItem_contents {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover; /* Cover the entire div */
  background-position: center; /* Center the image */
  color: white; /* Text color */
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.8); /* Text shadow for readability */
  background-blend-mode: darken; /* Darkens the background */
}

.movieTitle {
  align-self: flex-end; /* Push the text to the bottom */
  font-size: 14px; /* Slightly smaller text */
  font-weight: bold; /* Bold text */
  text-align: center; /* Center the text */
  width: 100%; /* Ensure text spans the container */
  padding: 5px; /* Add spacing for better readability */
  word-wrap: break-word; /* Wrap long movie names */
}